    getAverageRGB () {
      let imgEl = document.getElementById('lessonbg')
      let blockSize = 5 // only visit every 5 pixels
      let defaultRGB = { r: 0, g: 0, b: 0 } // for non-supporting envs
      let canvas = document.createElement('canvas')
      let context = canvas.getContext && canvas.getContext('2d')
      let data, width, height
      let i = -4
      let length
      let count = 0

      if (!context) {
        return defaultRGB
      }

      height = canvas.height = imgEl.naturalHeight || imgEl.offsetHeight || imgEl.height
      width = canvas.width = imgEl.naturalWidth || imgEl.offsetWidth || imgEl.width

      context.drawImage(imgEl, 0, 0)

      try {
        data = context.getImageData(0, 0, width, height)
      } catch (e) {
        // alert('x')
        return defaultRGB
      }

      length = data.data.length

      while ((i += blockSize * 4) < length) {
        ++count
        this.rgb.r += data.data[i]
        this.rgb.g += data.data[i + 1]
        this.rgb.b += data.data[i + 2]
      }

      // ~~ used to floor values
      this.rgb.r = ~~(this.rgb.r / count)
      this.rgb.g = ~~(this.rgb.g / count)
      this.rgb.b = ~~(this.rgb.b / count)
    },
