                <div class="mx-1 text-danger d-inline-block">{{ Math.ceil(readyFee * readySaleHour3 * readySaleOff3 / 100) }}</div>
                <span>元</span>
              </div>
            </h3>
          </v-radio-group>
        </div>
      </div>
      <hr>
      <div class="row">
        <div class="col-xs-12 col-md-12 text-center">
          <v-btn
            class="mx-4 my-4 p-4 h5 bg-button-color"
            @click="doSubmit('selling')">
            <v-icon class="mr-2">mdi-upload</v-icon>儲存課程並上架
          </v-btn>
          <v-btn
            class="mx-4 my-4 p-4 h5 bg-grey-color"
            @click="doSubmit('draft')">
            <v-icon class="mr-2">mdi-content-save</v-icon>儲存課程草稿
          </v-btn>
        </div>
        <!-- <pre style="word-wrap: break-word; white-space: pre-wrap;">{{ msg }}</pre> -->
      </div>
    </div>
    <v-snackbar
      v-model="snackbar"
      :top="true"
      :color="color">
      {{ errorMessages }}
      <v-btn
        dark
        flat
        @click="snackbar = false">
        Close
      </v-btn>
    </v-snackbar>
  </div>
</template>

<script>
