            }
          ]
        }
      ],
      readyOutlineReadOnly: '',
      readySaleUnit: true,
      readySaleTry: true,
      readySaleFeeTry: 200,
      readySale1: true,
      readySaleHour1: 10,
      readySaleOff1: 95,
      readySale2: true,
      readySaleHour2: 20,
      readySaleOff2: 80,
      readySale3: true,
