      if (e.srcElement.files.item(0).size > (4 * 1024 * 1024)) {
        $('#headshot-file').val('')
        alert('上傳照片不得超過4MB')
      } else {
        // alert('Uploading: ' + e.srcElement.files.item(0).name + ' @ ' + e.srcElement.files.item(0).size + 'bytes')
        // console.log(window.URL.createObjectURL(e.srcElement.files.item(0)))
        // console.log(e.srcElement.files.item(0).name)
        // let target = e.target
        // console.log(this.uploadIMG[i].url)
        // this.uploadIMG[i].url = this.getFileUrl(e.srcElement)
        this.regHeadshot[0].url = window.URL.createObjectURL(e.srcElement.files.item(0))
        this.regHeadshot[0].file = files[0]
        this.regHeadshot[0].name = e.srcElement.files.item(0).name
        // $('#img_' + target.id).attr('src', this.getFileUrl(e.srcElement))
      }
    },
    cancelHeadshotIMG () {
      this.regHeadshot[0].file = this.regHeadshot[0].name = this.regHeadshot[0].url = ''
      $('#headshot-file').val('')
      // this.createImage(files[0])
    },
    setImg (e, i) {
      let files = e.target.files || e.dataTransfer.files
      if (e.srcElement.files.item(0).size > (10 * 1024 * 1024)) {
        $('#file' + i).val('')
        alert('每個上傳檔案不得超過10MB')
      // if (e.srcElement.files.item(0).size > (4 * 1024 * 1024)) {
      //   alert('每個上傳檔案不得超過4MB')
      } else {
        // alert('Uploading: ' + e.srcElement.files.item(0).name + ' @ ' + e.srcElement.files.item(0).size + 'bytes')
        // console.log(window.URL.createObjectURL(e.srcElement.files.item(0)))
        // console.log(e.srcElement.files.item(0).name)
        // let target = e.target
        // console.log(this.uploadIMG[i].url)
        // this.uploadIMG[i].url = this.getFileUrl(e.srcElement)
        this.uploadIMG[i].url = window.URL.createObjectURL(e.srcElement.files.item(0))
        this.uploadIMG[i].file = files[0]
        this.uploadIMG[i].name = e.srcElement.files.item(0).name
        // $('#img_' + target.id).attr('src', this.getFileUrl(e.srcElement))
      }
    },
    cancelUploadIMG (i) {
      this.uploadIMG[i].file = this.uploadIMG[i].name = this.uploadIMG[i].url = ''
      $('#file' + i).val('')
      // this.createImage(files[0])
    },
    addUploadItem () {
      this.uploadIMG.push({ file: '', name: '', url: '' })
    },
    // getFileUrl (obj) {
    //   // console.log('getFileUrl')
    //   let url
    //   url = window.URL.createObjectURL(obj.files.item(0))
    //   // console.log(url)
    //   return url
    // },
    resSelectTime (val) {
      this.regSelectTime = val
    },
    regEmailFailMsg () {
      let isEmail = /^\w+((-\w+)|(\.\w+))*@[A-Za-z0-9]+((\.|-)[A-Za-z0-9]+)*\.[A-Za-z]+$/
      if (this.submitVerify) {
        if (this.regEmail === '') {
          this.regEmailVerify = true
          return 'Email不得為空'
        } else if (!isEmail.test(this.regEmail)) {
          this.regEmailVerify = true
          return 'Email輸入格式錯誤'
        } else {
          this.regEmailVerify = false
          return '✔'
        }
      } else {
        this.regEmailVerify = false
        return '請輸入您的Email'
      }
    },
    regPwdFailMsg () {
      if (this.submitVerify) {
        if (this.regPwd.length < 8) {
          this.regPwdVerify = true
          return '密碼請勿少於8個字'
        } else {
          this.regPwdVerify = false
          return '✔'
        }
      } else {
        this.regPwdVerify = false
        return '請輸入至少8位數密碼'
      }
    },
    regPwdConfirmFailMsg () {
      if (this.submitVerify) {
        if (this.regPwdConfirm === '') {
          this.regPwdConfirmVerify = true
          return '請再次輸入密碼'
        } else if (this.regPwd.length < 8) {
          this.regPwdVerify = true
          return '密碼請勿少於8個字'
        } else if (this.regPwdConfirm !== this.regPwd) {
          this.regPwdConfirmVerify = true
          return '密碼輸入不一致'
        } else {
          this.regPwdConfirmVerify = false
          return '✔'
        }
      } else {
        this.regPwdConfirmVerify = false
        return '請再次輸入密碼'
      }
    },
    regMobileFailMsg () {
      let isMobile = /^09[0-9]{2}-[0-9]{6}$/
      if (this.submitVerify) {
        if (this.regMobile === '') {
          this.regMobileVerify = true
          return '手機號碼不得為空，格式為09XX-XXXXXX'
        } else if (!isMobile.test(this.regMobile)) {
          this.regMobileVerify = true
          return '手機號碼輸入格式錯誤，格式為09XX-XXXXXX'
        } else {
          this.regMobileVerify = false
          return '✔'
        }
      } else {
        this.regMobileVerify = false
        return '請輸入您的手機號碼，格式為09XX-XXXXXX'
      }
    },
    regNameFailMsg () {
      if (this.submitVerify) {
        if (this.regName === '') {
          this.regNameVerify = true
          return '姓名不得為空'
        } else {
          this.regNameVerify = false
          return '✔'
        }
      } else {
        this.regNameVerify = false
        return '請輸入您的姓名'
      }
    },
    regNicknameFailMsg () {
      if (this.submitVerify) {
        if (this.regNickname !== '') {
          this.regNicknameVerify = false
          return '✔'
        } else {
          this.regNicknameVerify = false
          return '請輸入您的暱稱'
        }
      } else {
        this.regNicknameVerify = false
        return '請輸入您的暱稱'
      }
    },
    regYearFailMsg () {
      if (this.submitVerify) {
        if (this.regYear === '') {
          this.regYearVerify = true
          return '尚未選擇您的教學年資'
        } else {
          this.regYearVerify = false
          return '✔'
        }
      } else {
        this.regYearVerify = false
        return '請選擇您的教學年資'
      }
    },
    regGenderFailMsg () {
      if (this.submitVerify) {
        if (this.regGender === '') {
          this.regGenderVerify = true
          return '尚未選擇您的生理性別'
        } else {
          this.regGenderVerify = false
          return '✔'
        }
      } else {
        this.regGenderVerify = false
        return '請選擇您的生理性別'
      }
    },
    regBirthFailMsg () {
      let isBirth = /^[0-9]{4}-[0-9]{2}-[0-9]{2}$/
      if (this.submitVerify) {
        if (this.regBirth !== '' && !isBirth.test(this.regBirth)) {
          this.regBirthVerify = true
          return '生日輸入格式錯誤，格式為1900-01-01'
        } else if (this.regBirth !== '') {
          this.regBirthVerify = false
          return '✔'
        } else {
          return '請輸入您的生日，格式為1900-01-01'
        }
      } else {
        this.regBirthVerify = false
        return '請輸入您的生日，格式為1900-01-01'
      }
    },
    regIntroFailMsg () {
      if (this.submitVerify) {
        if (this.regIntro === '') {
          this.regIntroVerify = true
          return '自我介紹請至少填入一句話，上限150個字'
        } else if (this.regIntro.length > 150) {
          this.regIntroVerify = true
          return '自我介紹請輸入150字以內'
        } else {
          this.regIntroVerify = false
          return '✔'
        }
      } else {
        this.regIntroVerify = false
        return '請輸入150字以內的自我介紹'
      }
    },
    regTagFailMsg () {
      if (this.submitVerify) {
        if (this.regTag === '') {
          this.regTagVerify = true
          return '授課科目與對象請至少填入一門科目。'
        } else if (this.regTag.length > 500) {
          this.regTagVerify = true
          return '授課科目與對象請輸入500字以內。'
        } else {
          this.regTagVerify = false
          return '✔'
        }
      } else {
        this.regTagVerify = false
        return '請輸入授課科目與對象，並請用分號『;』隔開，如有特定學齡也請一併標註！範例：高中英文；多益；國中數學；高等微積分；成人商業英語。'
      }
    },
    regEdu1FailMsg () {
      if (this.submitVerify) {
        if (this.regEdu1 !== '') {
          this.regEdu1Verify = false
          return '✔'
        } else if (this.regEdu1 === '' && this.regEdu2 === '' && this.regEdu3 === '' && this.regJob === '' && this.regExp === '') {
          this.regEdu1Verify = true
          return '請至少輸入一項學經歷。'
        } else {
          this.regEdu1Verify = false
          return '請輸入第一學歷/科系，如正在就讀中請加寫年級。'
        }
      } else {
        this.regEdu1Verify = false
        return '請輸入第一學歷/科系，如正在就讀中請加寫年級。'
      }
    },
    regEdu2FailMsg () {
      if (this.submitVerify) {
        if (this.regEdu2 !== '') {
          this.regEdu2Verify = false
          return '✔'
        } else if (this.regEdu1 === '' && this.regEdu2 === '' && this.regEdu3 === '' && this.regJob === '' && this.regExp === '') {
          this.regEdu2Verify = true
          return '請至少輸入一項學經歷。'
        } else {
          this.regEdu2Verify = false
          return '請輸入第二學歷/科系，如正在就讀中請加寫年級。'
        }
      } else {
        this.regEdu2Verify = false
        return '請輸入第二學歷/科系，如正在就讀中請加寫年級。'
      }
    },
    regEdu3FailMsg () {
      if (this.submitVerify) {
        if (this.regEdu3 !== '') {
          this.regEdu3Verify = false
          return '✔'
        } else if (this.regEdu1 === '' && this.regEdu2 === '' && this.regEdu3 === '' && this.regJob === '' && this.regExp === '') {
          this.regEdu3Verify = true
          return '請至少輸入一項學經歷。'
        } else {
          this.regEdu3Verify = false
          return '請輸入第三學歷/科系，如正在就讀中請加寫年級。'
        }
      } else {
        this.regEdu3Verify = false
        return '請輸入第三學歷/科系，如正在就讀中請加寫年級。'
      }
    },
    regJobFailMsg () {
      if (this.submitVerify) {
        if (this.regJob !== '') {
          this.regJobVerify = false
          return '✔'
        } else if (this.regEdu1 === '' && this.regEdu2 === '' && this.regEdu3 === '' && this.regJob === '' && this.regExp === '') {
          this.regJobVerify = true
          return '請至少輸入一項學經歷。'
        } else {
          this.regJobVerify = false
          return '請輸入現任單位/職稱。'
        }
      } else {
        this.regJobVerify = false
        return '請輸入現任單位/職稱。'
      }
    },
    regExpFailMsg () {
      if (this.submitVerify) {
        if (this.regExp !== '') {
          this.regExpVerify = false
          return '✔'
        } else if (this.regEdu1 === '' && this.regEdu2 === '' && this.regEdu3 === '' && this.regJob === '' && this.regExp === '') {
          this.regExpVerify = true
          return '請至少輸入一項學經歷。'
        } else {
          this.regExpVerify = false
          return '請輸入其他學經歷或特殊專長。'
        }
      } else {
        this.regExpVerify = false
        return '請輸入其他學經歷或特殊專長。'
      }
    },
    // regHeadshotFailMsg () {
    //   if (this.submitVerify) {
    //     if ($('#headshot-file').val() === '') {
    //       this.regHeadshotVerify = true
    //       return '請務必上傳頭像。'
    //     } else {
    //       this.regHeadshotVerify = false
    //       return '✔'
    //     }
    //   } else {
    //     this.regHeadshotVerify = false
    //     return '請上傳頭像。'
    //   }
    // },
    // regSelectTimeFailMsg () {
    //   if (this.submitVerify) {
    //     if (this.regSelectTime === []) {
    //       this.regHeadshotVerify = true
    //       return '請務必選擇常態開課時段。'
    //     } else {
    //       this.regHeadshotVerify = false
    //       return '✔'
    //     }
    //   } else {
    //     this.regHeadshotVerify = false
    //     return '請選擇常態開課時段。'
    //   }
    // },
    dosubmitVerifyBefore () {
      this.submitVerify = true
    },
    doSubmitVerify () {
      if (!this.regEmailVerify &&
      !this.regPwdVerify &&
      !this.regPwdConfirmVerify &&
      !this.regMobileVerify &&
      !this.regNameVerify &&
      !this.regNicknameVerify &&
      !this.regGenderVerify &&
      !this.regYearVerify &&
      !this.regBirthVerify &&
      !this.regIntroVerify &&
      !this.regTagVerify &&
      !this.regEdu1Verify &&
      !this.regEdu2Verify &&
      !this.regEdu3Verify &&
      !this.regJobVerify &&
      !this.regExpVerify) {
        this.submit = true
      } else {
        this.submit = false
        scroll(0, 0)
        this.snackbar = true
        this.color = 'red'
        this.errorMessages = '請修正錯誤欄位後再送出'
      }
    },
    dosubmitVerifyAfter () {
      if (this.submit) {
        let formData = new FormData()
        formData.append('regEmail', this.regEmail)
        formData.append('regPwd', this.regPwd)
        formData.append('regMobile', this.regMobile)
        formData.append('regName', this.regName)
        formData.append('regNickname', this.regNickname || this.regName)
        formData.append('regGender', this.regGender)
        formData.append('tutor_experience', this.regYear)
        formData.append('regBirth', this.regBirth)
        formData.append('intro', this.regIntro)
        formData.append('subject_type', this.regTag)
        formData.append('education_1', this.regEdu1)
        formData.append('education_2', this.regEdu2)
        formData.append('education_3', this.regEdu3)
        formData.append('company', this.regJob)
        formData.append('special_exp', this.regExp)
        formData.append('teacher_general_availabale_time', this.regSelectTime)
        formData.append('upload_snapshot', this.regHeadshot[0].file)
        this.uploadIMG.forEach((up) => {
          formData.append('upload_cer', up.file)
        })
        this.$http.post('api/account/signupTeacher/', formData).then(res => res.data)
          .then(res => {
            if (res.status === 'success') {
              this.snackbar = true
              this.color = 'green'
              this.errorMessages = '您的申請將交由專人審核，於1~3日內通知審核結果，請您密切注意信箱！'
              this.$router.push('/')
            } else if (res.status === 'failed') {
              this.snackbar = true
              this.color = 'red'
              this.errorMessages = res.errMsg
            } else {
              this.snackbar = true
              this.color = 'orange'
              this.errorMessages = '發生錯誤，請稍後再試'
            }
          })
          .catch(err => {
            this.snackbar = true
            this.color = 'orange'
            this.errorMessages = '發生錯誤，請稍後再試'
            console.log(err)
          })
      }
    },
    async doSubmit () {
      await this.dosubmitVerifyBefore()
      await this.doSubmitVerify()
      await this.dosubmitVerifyAfter()
    }
  }
}
</script>
